	font-size: 3.5rem;
}
h3 {
	font-size: 3rem;
}
h4 {
	font-size: 2.5rem;
}
h5 {
	font-size: 2rem;
}
h6 {
	font-size: 1.5rem;
}
p,
.text {
	font-size: 0.875rem;
	color: #4f4f4f;
}
@media (max-width: 640px) {
	/* sm */
	.retailer-ui-container {
		padding: 0rem 1rem;
	}
	.nav-link,
	nav li,
	nav li a {
		font-size: 1.5rem;
	}
}
