	}
}
.link-text,
a {
	color: #da1a35;
}

.link-text:hover,
a:hover {
	text-decoration: underline;
	cursor: pointer;
}

.nav-link:hover,
nav li:hover,
nav li a:hover {
	cursor: pointer;
	text-decoration: none;
}

button,
.button {
	color: white;
	padding: 0.5rem 4rem;
	background-color: rgb(48, 48, 48);
	display: flex;
	width: fit-content;
	font-size: 1.25rem;
}

.button-outline {
	outline: 1px solid rgb(48, 48, 48);
	background-color: transparent;
	color: rgb(48, 48, 48);
	display: flex;
	width: fit-content;
	padding: 0.5rem 4rem;
	font-size: 1.25rem;
}

.first-comp-margin {
	margin-top: 12vh;
}
.flex-col {
	display: flex;
	flex-direction: column;
}
.fit-content {
	width: fit-content;
	height: fit-content;
}
.rigt-third-center-content {
	margin: auto auto auto 66%;
}
.center-content {
	margin: auto;
}
.bg-red {
	background-color: red;
}
.expand-mobile-nav {
	width: 100vh;
	z-index: 15;
	transform: translateX(0rem);
	opacity: 1;
}
.h-18vh {
	height: 18vh;
}
